import React, { Component, memo } from "react";
import queryString from 'query-string';
import { isMobile, isTablet } from 'react-device-detect';
import { Helmet } from "react-helmet";
import { CloudUrl, BunnyCloudUrl } from "../../../constants";
import { getThemeImageFromWallID, getCurrentMilliseconds, getCurrentSeconds, getCurrentMinutes, getCurrentHours, getTextColor, rgbaToRgb } from '../../../utils'
import { hashtagCampaignImage } from '../../../actions/themeActions'
import { FOR_TEST_SERVER, suiteTrack, isTagEmbed, isTagShop, isTagShopEditor, isTagbox } from "../../../actions/api";
import { StyleConstant } from "./StyleConstant";

const defaultURL = `https://cloud.taggbox.com/wall-branding/wallId_widget_`
const currentTime = getCurrentMilliseconds() + 1000 * (getCurrentSeconds() + 60 * (getCurrentMinutes() + 60 * getCurrentHours()));

const TwitterScriptRender = () => {
    return <script async>
        {`window.twttr = (function (d, s, id) {
                            var js, fjs = d.getElementsByTagName(s)[0],
                                t = window.twttr || {};
                            if (d.getElementById(id)) return t;
                            js = d.createElement(s);
                            js.id = id;
                            js.src = "https://platform.twitter.com/widgets.js";
                            fjs.parentNode.insertBefore(js, fjs);

                            t._e = [];
                            t.ready = function (f) {
                                                    t._e.push(f);
                            };

                            return t;
                            }(document, "script", "twitter-wjs"));`
        }
    </script>
}

